import { NgForm } from '@angular/forms';
import { KeyValue } from '@angular/common';
import { BackServiceMapData, listas } from 'src/app/models/back-service.model';
import { IDropdownSettings } from 'ng-multiselect-dropdown';
import { Component, OnInit, ViewChild } from '@angular/core';
import { UtilsService } from 'src/app/services/utils.service';
import { CompanyService } from 'src/app/services/company.service';
import { ExposurMapService } from 'src/app/services/exposure_map.service';
import { BackServiceMap, BackServiceStand } from 'src/app/models/back-service.model';

@Component({
  selector: 'app-admin-stands',
  templateUrl: './admin-stands.component.html',
  styleUrls: ['./admin-stands.component.css']
})
export class AdminStandsComponent implements OnInit {
  @ViewChild('form') form!: NgForm;

  public view = 0;
  public active = 1;
  public eventID: number = 0;
  public filtro: string = '';
  public errorMaps: any[] = [];
  public loading: boolean = false;
  public disabled: boolean = false;
  public isShowDiv: boolean = true;
  public categoriesHeading: string[] = [];
  public list: {}[] = [{ 'Nombre de empresa': '', 'Número de stand': '', 'Nivel del stand': '' }];

  public dropdownSettings: IDropdownSettings = {
    idField: 'id',
    textField: 'name',
    itemsShowLimit: 1,
    enableCheckAll: false,
    singleSelection: true,
    allowSearchFilter: true,
    clearSearchFilter: false,
    closeDropDownOnSelection: false,
    searchPlaceholderText: 'Buscar',
    noDataAvailablePlaceholderText: 'Sin resultados en la busqueda...',
  };

  //TEMPORAL PARA TEST
  public company: any = null;
  public floor: any = null;

  // public floorList = [{ id: 1, name: 'Piso 1' }, { id: 2, name: 'Piso 2' }, { id: 3, name: 'Piso 3' }, { id: 4, name: 'Piso 4' }]
  public floorList = [
    { id: 1, key: 1, name: 'Piso 1' },
    { id: 2, key: 2, name: 'Piso 2' },
    { id: 3, key: 3, name: 'Piso 3' },
    { id: 4, key: 4, name: 'Piso 4' }
  ]

  onSelect(type: string, item: any) {
    console.log(type, item);
    switch (type) {
      case 'company':
        this.stand.companyName = item.name;
        break;
      case 'floor':
        this.stand.floorId = item.id;
        break;
    }
  }

  onDeSelect(type: string, item: any) {
    console.log(type, item);
    switch (type) {
      case 'company':
        this.stand.companyName = undefined;
        break;
      case 'floor':
        this.stand.floorId = 0;
        break;
    }
  }

  clickDropDown(name: string) {
    this.form.controls[name].markAsTouched();
    this.form.controls[name].markAsTouched();
  }

  public userID: number = 0;
  public eventName: string = '';
  public eventAddress: string = '';
  public companyList: any | listas = [];
  public map: BackServiceMap = {} as BackServiceMap;
  public stand: BackServiceStand = {} as BackServiceStand;
  public mapData: BackServiceMapData = {} as BackServiceMapData;

  constructor(
    private utilsService: UtilsService,
    private companyService: CompanyService,
    private exposurMapService: ExposurMapService
  ) { this.categoriesHeading = Object.keys(this.list[0]); }

  async ngOnInit(): Promise<void> {
    this.isShowDiv = true;
    const token: string | undefined = sessionStorage.getItem('Token')?.toString();
    this.userID = Number(sessionStorage.getItem('UserID'));
    this.eventID = Number(sessionStorage.getItem('EventID'));

    const eventData = JSON.parse(sessionStorage.getItem('EventData') || '');
    this.eventName = eventData.nombreEvento ? eventData.nombreEvento.toString() : '';
    this.eventAddress = eventData.direccion ? eventData.direccion.toString() : '';

    if (token && this.eventID > 0) {
      this.cleanModel();
      await this.loadAllCompany();
      await this.loadDataStand(this.eventID);
    }
    else if (token && this.eventID === 0) {
      this.utilsService.dialog('ERROR', 'Error', 'No tienes seleccionado un evento');
      this.utilsService.goToPage('/admin/events/calendario-eventos');
    }
    else {
      this.utilsService.dialog('ERROR', 'Error', 'No tienes seleccionado un evento');
      this.utilsService.goToPage('/login');
    }
    // console.log(this.floor);
  }

  showActions(event: any) {
    this.isShowDiv = true;
    const element = document.getElementById('item' + event);
    const listItes = document.getElementsByClassName('actions-disble')[0];
    if (listItes) {
      listItes.classList.remove('actions-disble');
    }
    if (('item' + event) !== listItes?.id || !listItes.id) {
      element?.classList.add('actions-disble');
    }
  }

  originalOrder = (a: KeyValue<string, string>, b: KeyValue<string, string>): number => {
    return 0;
  }

  validation_msg(name: string): boolean {
    if (this.form && this.form.controls[name]) {
      if (
        (this.form.controls[name].errors || this.form.controls[name].invalid)
        &&
        (this.form.controls[name].touched) // || this.form.controls[name].dirty)
      ) {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName)
  }

  cleanModel() {
    this.map = {} as BackServiceMap;
    this.stand = {} as BackServiceStand;
    // this.map.eventId = this.eventID;
    this.map.userCreated = this.userID;
    this.mapData.eventName = this.eventName;
    this.stand.userCreated = this.userID;
    this.mapData.eventAddress = this.eventAddress;
    this.stand.image64 = '';
    this.stand.image = { name: '', extension: '' };
    this.mapData.imageFloorOne = { id: null, name: '', data: '', extension: '', url: '' };
    this.mapData.imageFloorTwo = { id: null, name: '', data: '', extension: '', url: '' };
    this.mapData.imageFloorFour = { id: null, name: '', data: '', extension: '', url: '' };
    this.mapData.imageFloorThree = { id: null, name: '', data: '', extension: '', url: '' };
  }

  async changeView(view: number, active?: number) {
    this.view = view;
    (view === 1) && (this.loadDataMap(this.eventID));
    (active) && (this.active = active);
    await this.cleanModel();
  }

  changeTab(type: string) {
    // console.log('type: ', type, 'view: ', this.view);

    if (type == 'stand' && this.view == 0) {
      this.loadDataStand(this.eventID);
    }
    else if (type == 'stand' && this.view == 1) {

    }
    else if (type == 'map') {
      this.loadDataMap(this.eventID);
    }
  }

  loadAllCompany() {
    this.loading = true;
    return this.companyService.getListCompany(0, 2500).then(
      response => {
        this.companyList = response.result.map((x: any) => {
          return {
            id: x.id,
            name: x.nombreComercial
          }
        });
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Error al cargar empresas!');
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  loadDataStand(event_id: number) {
    this.loading = true;
    return this.exposurMapService.getStandByEvent(event_id).then(
      response => {
        console.log(response)
        if (response.result.length > 0) {
          this.list = response.result.map((e: any) => {
            return {
              id: e?.idStand,
              'Nombre de empresa': e?.companyName,
              'Número de stand': e?.idStand,
              'Nivel del stand': e?.stand
            }
          });
        } else {
          this.list = [];
          this.active = 2;
          this.changeTab('map')
          this.utilsService.dialog('ERROR', 'Alerta', '¡No hay datos de piso por favor ingres los datos!');
        }
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Error al cargar la información!');
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  loadDataMap(event_id: number) {
    this.loading = true;
    return this.exposurMapService.getMapByEvent(event_id).then(
      response => {
        console.log(response)
        if (response.result[0].floors.length > 0) {
          const floor_1 = response.result[0].floors.find((x: any) => x.floor == 1);
          const floor_2 = response.result[0].floors.find((x: any) => x.floor == 2);
          const floor_3 = response.result[0].floors.find((x: any) => x.floor == 3);
          const floor_4 = response.result[0].floors.find((x: any) => x.floor == 4);

          this.floorList = [];
          if (floor_1) {
            const image = floor_1.image.split('/');
            this.mapData.imageFloorOne.name = image[7];
            this.mapData.imageFloorOne.url = floor_1.image;
            this.mapData.imageFloorOne.id = floor_1.idFloor;
            this.floorList.push({ id: floor_1.idFloor, key: floor_1.idFloor, name: 'Piso 1' });
          }

          if (floor_2) {
            const image = floor_2.image.split('/');
            this.mapData.imageFloorTwo.name = image[7];
            this.mapData.imageFloorTwo.url = floor_2.image;
            this.mapData.imageFloorTwo.id = floor_2.idFloor;
            this.floorList.push({ id: floor_2.idFloor, key: floor_2.idFloor, name: 'Piso 2' });
          }

          if (floor_3) {
            const image = floor_3.image.split('/');
            this.mapData.imageFloorThree.name = image[7];
            this.mapData.imageFloorThree.url = floor_3.image;
            this.mapData.imageFloorThree.id = floor_3.idFloor;
            this.floorList.push({ id: floor_3.idFloor, key: floor_3.idFloor, name: 'Piso 3' });
          }

          if (floor_4) {
            const image = floor_4.image.split('/');
            this.mapData.imageFloorFour.name = image[7];
            this.mapData.imageFloorFour.url = floor_4.image;
            this.mapData.imageFloorFour.id = floor_4.idFloor;
            this.floorList.push({ id: floor_4.idFloor, key: floor_4.idFloor, name: 'Piso 4' });
          }
        }
        // console.log(this.mapData);
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Error al cargar la información!');
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  openImage(url: string) {
    if (url != '') {
      window.open(url, '_blank');
    }
  }

  async addFile(event: any) {
    let floor_id: any;
    this.form.control.markAsTouched();
    const file = event.target.files[0];
    const extension = file.type.split('/')
    const base64 = await this.utilsService.convertBase64(event.target.files.item(0));

    if (typeof (base64) === 'string') {
      switch (event.target.id) {
        case 'input-images-file-logo':
          this.stand.image64 = base64;
          this.stand.image = { extension: extension[1], name: file.name }
          break;
          case 'input-images-file-map-1':
          floor_id = this.mapData.imageFloorOne.id != null ? this.mapData.imageFloorOne.id : 0;
          this.mapData.imageFloorOne = { id: floor_id, extension: extension[1], name: file.name, data: base64, url: '' }
          break;
        case 'input-images-file-map-2':
          floor_id = this.mapData.imageFloorTwo.id != null ? this.mapData.imageFloorTwo.id : 0;
          this.mapData.imageFloorTwo = { id: floor_id, extension: extension[1], name: file.name, data: base64, url: '' }
          break;
        case 'input-images-file-map-3':
          floor_id = this.mapData.imageFloorThree.id != null ? this.mapData.imageFloorThree.id : 0;
          this.mapData.imageFloorThree = { id: floor_id, extension: extension[1], name: file.name, data: base64, url: '' }
          break;
        case 'input-images-file-map-4':
          floor_id = this.mapData.imageFloorFour.id != null ? this.mapData.imageFloorFour.id : 0;
          this.mapData.imageFloorFour = { id: floor_id, extension: extension[1], name: file.name, data: base64, url: '' }
          break;
      }
    }
  }

  async processData(active: number) {
    if (this.form.valid) {
      if (active === 1) {
        this.createStand();
      }
      else if (active === 2) {
        this.loading = true;
        this.disabled = true;
        for (let i = 0; i < 4; i++) {
          const index = i + 1;
          this.map.floorNumber = index;
          this.map.eventMapId = this.eventID;
          this.map.userCreated = this.userID;
          switch (index) {
            case 1:
              this.map.image64 = this.mapData.imageFloorOne.data || '';
              this.map.idEventMap = this.mapData.imageFloorOne.id || 0;

              if (this.map.idEventMap === 0 && this.mapData.imageFloorOne.data != '') {
                // CREAR 'base64/texto' 0
                console.log('No: ' + index + ' crear');
                // await this.createMap(this.map)
              } else if (this.map.idEventMap > 0 && this.mapData.imageFloorOne.data != '') {
                // EDITAR '' 5
                console.log('No: ' + index + ' actualizar')
                // await this.upadteMap(this.map)
              } else {
                // VACIO '' null
                console.log('No: ' + index + ' no hacer nada')
              }
              break;
            case 2:
              this.map.image64 = this.mapData.imageFloorTwo.data;
              this.map.idEventMap = this.mapData.imageFloorTwo.id || 0;

              if (this.map.idEventMap === 0 && this.mapData.imageFloorTwo.data != '') {
                // CREAR 'base64/texto' 0
                console.log('No: ' + index + ' crear');
                // await this.createMap(this.map)
              } else if (this.map.idEventMap > 0 && this.mapData.imageFloorTwo.data != '') {
                // EDITAR '' 5
                console.log('No: ' + index + ' actualizar')
                // await this.upadteMap(this.map)
              } else {
                // VACIO '' null
                console.log('No: ' + index + ' no hacer nada')
              }
              break;
            case 3:
              this.map.image64 = this.mapData.imageFloorThree.data;
              this.map.idEventMap = this.mapData.imageFloorThree.id || 0;
              this.map.idEventMap == null &&  this.mapData.imageFloorThree.data != ''
              if (this.map.idEventMap === 0 && this.mapData.imageFloorThree.data != '') {
                // CREAR 'base64/texto' 0
                console.log('No: ' + index + ' crear');
                // await this.createMap(this.map)
              } else if (this.map.idEventMap > 0 && this.mapData.imageFloorThree.data != '') {
                // EDITAR '' 5
                console.log('No: ' + index + ' actualizar')
                // await this.upadteMap(this.map)
              } else {
                // VACIO '' null
                console.log('No: ' + index + ' no hacer nada')
              }
              break;
            case 4:
              this.map.image64 = this.mapData.imageFloorFour.data;
              this.map.idEventMap = this.mapData.imageFloorFour.id || 0;

              if (this.map.idEventMap === 0 && this.mapData.imageFloorFour.data != '') {
                // CREAR 'base64/texto' 0
                console.log('No: ' + index + ' crear');
                // await this.createMap(this.map)
              } else if (this.map.idEventMap > 0 && this.mapData.imageFloorFour.data != '') {
                // EDITAR '' 5
                console.log('No: ' + index + ' actualizar')
                // await this.upadteMap(this.map)
              } else {
                // VACIO '' null
                console.log('No: ' + index + ' no hacer nada')
              }
              break;
          }
          this.loading = false;
          this.disabled = false;

          if (this.errorMaps.includes(true)) {
            this.utilsService.dialog('ERROR', 'Error', '¡Error al editado mapa!');
          } else {
            this.utilsService.dialog('SUCCESS', 'Éxito', '¡Mapa editado!');
          }
        }
      }
    } else {
      this.form.control.markAllAsTouched();
      this.utilsService.dialog('ERROR', 'Error', 'Por favor llena los campos correctamente.');
    }
  }

  createStand() {
    this.loading = true;
    this.disabled = true;
    this.exposurMapService.createStand(this.stand).then(
      (response) => {
        this.loadDataStand(this.eventID);
        this.utilsService.dialog('SUCCESS', 'Éxito', response?.message?.descripcion || '¡Stand creado!');
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', error?.error?.message?.descripcion || '¡Error al crear stand!');
      }).finally(() => {
        this.loading = false;
        this.disabled = false;
      });
  }

  createMap(map: BackServiceMap) {
    return this.exposurMapService.createMap(map).then(
      (response) => {
        console.log(response);
        this.errorMaps.push(false)
      },
      error => {
        console.log(error);
        this.errorMaps.push(true)
      }
    );
  }

  upadteMap(map: BackServiceMap) {
    return this.exposurMapService.updateMap(map).then(
      (response) => {
        console.log(response);
        this.errorMaps.push(false)
      },
      error => {
        console.log(error);
        this.errorMaps.push(true)
      }
    );
  }

  deletedStand(ID: any) {
    this.loading = true;
    this.exposurMapService.deleteStand(ID).then(
      async (response) => {
        await this.loadDataStand(this.eventID);
        this.utilsService.dialog('SUCCESS', 'Éxito', response?.message?.descripcion || '¡El stand se elimino!');
      },
      error => {
        this.utilsService.dialog('ERROR', 'Error', error?.error?.message?.descripcion || '¡El stand no se elimino!');
      }).finally(() => {
        this.loading = false
      })
  }
}
